import { useState } from 'react';
import { useNavigate } from "react-router-dom";
// @mui
import { useTheme } from '@mui/material/styles';
import {
  Box,
  Button,
  Checkbox,
  Divider,
  FormControl,
  FormControlLabel,
  FormHelperText,
  IconButton,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Stack,
  Typography
} from '@mui/material';

// third party

import * as Yup from 'yup';
import { Formik, FormikProvider, useFormik } from 'formik';

// project
import useScriptRef from 'hooks/useScriptRef';
import { useAuth } from 'providers/Auth'
import AnimateButton from 'components/button/AnimateButton';

// assets
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

import Google from 'assets/images/icons/social-google.svg';
import Facebook from 'assets/images/icons/social-facebook.svg';
import Twitter from 'assets/images/icons/social-twitter.svg';
import LinkedIn from 'assets/images/icons/social-linkedin.svg';
import { post } from 'utils/request'
// import Auth from 'utils/Auth'
import _ from 'lodash'
// -----------------------------------------------------------------------------------

const FirebaseLogin = ({ ...others }) => {

  const theme = useTheme();
  const navigate = useNavigate();
  // const scriptedRef = useScriptRef();
  const [errs, setErrs] = useState(false)
  // const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));
  const [checked, setChecked] = useState(true);
  const { setAuth } = useAuth()
  const googleHandler = async () => {
    console.error('Login');
  };

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };


  const SigninSchema = Yup.object().shape({
    email: Yup.string()
      .email('Must be a valid email')
      .max(255)
      .required('Email is required'),
    password: Yup.string().max(255).required('Password is required'),
    submit: null
  });


  const formik = useFormik({
    initialValues: {
      email: 'yxz20180810@gmail.com',
      password: '123456'
    },
    validationSchema: SigninSchema,
    onSubmit: (values) => {
      // const history = createHashHistory();
      // history.replace('/login'); 
      // post to backend     
      const payload = {
        email: values["email"],
        password: values["password"]
      };

      post('http://localhost:8080/auth/login', payload)
        .then(resp => {
          console.log("resp:", resp)
          if (resp.success) {
            setAuth({ token: resp.token, expired: resp.expired })
            navigate('/')
          } else {
            setErrs(true);
          }
        }).catch(error => {
          // eslint-disable-next-line no-console
          console.error("error:", error);
        });
    }
  });

  const {
    errors,
    touched,
    getFieldProps,
    handleBlur,
    handleChange,
    handleSubmit,
    isSubmitting,
    values
  } = formik;


  return (
    <>
      <Stack direction={'row'} justifyContent={'center'} spacing={2} mb={3}>
        <IconButton
          color="primary"
          aria-label=""
          component="label"
          onClick={googleHandler}
        >
          <img src={Google} alt="google" width={36} height={36} />
        </IconButton>
        <IconButton
          color="primary"
          aria-label=""
          component="label"
          onClick={googleHandler}
        >
          <img src={Twitter} alt="Twitter" width={36} height={36} />
        </IconButton>
        <IconButton
          color="primary"
          aria-label=""
          component="label"
          onClick={googleHandler}
        >
          <img src={Facebook} alt="Facebook" width={36} height={36} />
        </IconButton>
        <IconButton
          color="primary"
          aria-label=""
          component="label"
          onClick={googleHandler}
        >
          <img src={LinkedIn} alt="LinkedIn" width={36} height={36} />
        </IconButton>
      </Stack>
      <Divider sx={{ my: 3 }}>or</Divider>
      <FormikProvider value={formik}>
        <form noValidate onSubmit={handleSubmit} {...others}>
          <Stack spacing={2}>
            <FormControl
              fullWidth
              error={Boolean(touched.email && errors.email)}
              sx={{ ...theme.typography.customInput }}
            >
              <InputLabel htmlFor="outlined-adornment-email-login">
                Email Address / Username
              </InputLabel>
              <OutlinedInput
                id="outlined-adornment-email-login"
                type="email"
                value={values.email}
                name="email"
                onBlur={handleBlur}
                onChange={handleChange}
                label="Email Address / Username"
                inputProps={{}}
              />
              {touched.email && errors.email && (
                <FormHelperText
                  error
                  id="standard-weight-helper-text-email-login"
                >
                  {errors.email}
                </FormHelperText>
              )}
            </FormControl>

            <FormControl
              fullWidth
              error={Boolean(touched.password && errors.password)}
              sx={{ ...theme.typography.customInput }}
            >
              <InputLabel htmlFor="outlined-adornment-password-login">
                Password
              </InputLabel>
              <OutlinedInput
                id="outlined-adornment-password-login"
                type={showPassword ? 'text' : 'password'}
                value={values.password}
                name="password"
                onBlur={handleBlur}
                onChange={handleChange}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                      edge="end"
                      size="large"
                    >
                      {showPassword ? <Visibility /> : <VisibilityOff />}
                    </IconButton>
                  </InputAdornment>
                }
                label="Password"
                inputProps={{}}
              />
              {touched.password && errors.password && (
                <FormHelperText
                  error
                  id="standard-weight-helper-text-password-login"
                >
                  {errors.password}
                </FormHelperText>
              )}
            </FormControl>
            <Stack
              direction="row"
              alignItems="center"
              justifyContent="space-between"
              spacing={1}
            >
              <FormControlLabel
                control={
                  <Checkbox
                    checked={checked}
                    onChange={(event) => setChecked(event.target.checked)}
                    name="checked"
                    color="primary"
                  />
                }
                label="Remember me"
              />
              <Typography
                variant="subtitle1"
                color="text.default"
                sx={{ textDecoration: 'none', cursor: 'pointer' }}
              >
                Forgot Password?
              </Typography>
            </Stack>
            {errors.submit && (
              <Box sx={{ mt: 3 }}>
                <FormHelperText error>{errors.submit}</FormHelperText>
              </Box>
            )}
            <Box sx={{ mt: 2 }}>
              <AnimateButton>
                <Button
                  disableElevation
                  disabled={isSubmitting}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained"
                // color="secondary"
                >
                  Sign in
                </Button>
              </AnimateButton>
            </Box>
          </Stack>
        </form>
      </FormikProvider>
    </>
  );
};

export default FirebaseLogin;
